@import url(https://fonts.googleapis.com/css?family=Work+Sans:400,600);
@import url(https://fonts.googleapis.com/css?family=Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  --background-color: #FFFF80;
  --h1-color:#272727;
  --h1s-color:rgb(206, 192, 30,0.3);
  /* --h1s-color:rgb(177, 164, 21); */
  --h2-color: #272727;
  --button-icon-color:#faf881;
  --button-color-background1: rgb(186,186,186);
  --button-color-background2: radial-gradient(circle, rgba(186,186,186,1) 45%, rgba(129,129,129,1) 100%);
}

html[data-theme="dark"] {
  --background-color: #272727;
  --h1-color:rgb(241, 245, 39);
  --h1s-color:rgba(242, 245, 39, 0.3);
  --h2-color: #fff;
  --button-icon-color:#FFFBA0;
  --button-color-background1: rgba(255,234,1);
  --button-color-background2: radial-gradient(circle, rgba(255,234,1) 0%, rgb(251,171,0,1) 100%);

}

body {
  font-family: 'Rubik', sans-serif;
