.block-heading{
  margin-bottom: 0px;
  /* font-weight: bold; */
  
}

.block-description{
  margin-bottom: 10px;
}

.welcome {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

div.page-title {
  width:100%;
  text-align: center;
  justify-content: space-around;
  align-items: center;
  margin-top: 100px;
  padding-bottom: 10px;
}

a.social-icon {
  color: #272727;
  color: var(--h1-color);
  margin-right: 1rem;
  margin-bottom: 1rem;
  transition: transform 250ms;
  display: inline-block;
}

a.social-icon:hover {
  color: #272727;
  color: var(--h2-color);
  transform: translateY(-5px);
}

img {
  width: 100%;
  height: auto;
}

.item-img {
  border-radius: 10px;
  transition: transform 250ms;
}

.item-img:hover{
  border-radius: 0px;
}

.showcase {
  width:80%;
  align-items:center;
  position:'relative'
}

.showcase-item{
  transition: transform 250ms;
}

.showcase-item:hover{
  transform: translateY(-15px);
}

.showcase-item::before {
  content: '';
  display: block;
  height: 5px;
  background-color: #272727;
  background-color: var(--h1-color);
  top: 0;
  width: 0%;
  transition: all ease-in-out 250ms;
}

.showcase-item:hover::before {
  width: 100%;
}


.showcase-item-details{
  margin-top:10px;
}

button.fab-item{
  margin:0px;
}

nav{
  margin-top: 0px;
  padding-top: 5px;
  padding-bottom:5px;
  align-items: center;
  position: fixed;
  top: 0;
  left:0;
  width: 100%;
  background-color:#FFFF80;
  background-color:var(--background-color);
}

nav ul{
  margin: 0;
  padding: 0;
  /* list-style: none; */
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
}

nav li.nav-links {
  display: inline-block;
  position: relative;
  text-align: center;
	font-weight: 800;
  color: #272727;
  color: var(--h1-color);
  text-decoration: none;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  font-size: 14px;
}

nav li.nav-links:hover {
  color: #272727;
  color: var(--h2-color);
}

nav li.nav-links::before {
  content: '';
  display: block;
  height: 5px;
  background-color: #272727;
  background-color: var(--h1-color);
  
  top: 0;
  width: 0%;

  transition: all ease-in-out 250ms;
}

nav li.nav-links:hover::before {
  width: 100%;
}

a:hover {
  color:none;
  text-decoration:none;
}

.skill-barbox{
  width:100%;
  position:relative;
  background:rgb(206, 192, 30,0.3);
  background:var(--h1s-color);
  border-radius:3px;
  box-sizing: border-box;
}

.skill-barlevel{
  background:#272727;
  background:var(--h1-color);
  height:100%;
  border-radius:3px;
  box-sizing: border-box;
  animation: barlevel 4s;
  -webkit-animation: barlevel 4s;
  -moz-animation: barlevel 4s;
  -o-animation: barlevel 4s;

}

@keyframes barlevel {
  0%{
    width:0%;
  }
  100%{
  }
}


@-webkit-keyframes barlevel {
  0%{
    width:0%;
  }
  100%{
  }
}

@-webkit-keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.fab-container{
  position: fixed;
  display: inline-block;
  bottom: 110px;
  right:20px;
  align-items: right;
}

.fab {
  padding-top: 15px;
  padding-left: 15px;
  align-items: center;
  border-radius: 50%;
  background:#FFFF80;
  background:var(--background-color);
  width: 60px;
  height: 60px;
  box-shadow: 3px 3px #000000;
  transition: transform 100ms;
}

.fab:active {
  transform: translateY(5px);
}

.fab-link:hover{
  opacity:0.5;
}

.fab-link:hover + .fab-options {
  opacity:0.75;
  display:block;
}

.dribble-background{
  background:rgb(255, 255, 255);
  border-radius: 5px;
  vertical-align: middle;
  padding:3px;
  height:35px;
  line-height: 28px;
  text-align: center;
  box-shadow: 3px 3px #000000;
  white-space: nowrap;

}

.spin{
  color:#272727;
  color:var(--h1-color);
  -webkit-animation: rotation 8s infinite linear;
          animation: rotation 8s infinite linear;
}

.switch {
  align-items: right;
  position: fixed;
  display: inline-block;
  bottom: 40px;
  right:20px;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgb(130, 130, 130);
  transition: .4s;
  box-shadow: 3px 3px #000000;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: rgb(186, 186, 186);
  box-shadow: 1px 1px #000000;
  transition: .4s;
}

input:checked+.slider {
  background-color: rgb(255, 234, 0);
  box-shadow: 3px 3px #000000;
}

input:focus+.slider {
  box-shadow: 3px 3px #000000;
}

input:checked+.slider:before {
  transform: translateX(26px);
}

.on
{
  display: none;
}

.on, .off
{
  color:#272727;
  position: absolute;
  transform: translate(-50%,-50%);
  top: 50%;
}

.on{
  left: 30%;
  font-size: 20px;
}

.off{
left: 75%;
font-size: 20px;
}

input:checked+ .slider .on
{display: block;}

input:checked + .slider .off
{display: none;}


/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

